.main{
	display: grid;
	grid-template-rows: 270px 200px;

}

.logos{
	display: grid;
	margin-left: auto;
	margin-right: auto;
	grid-template-columns: auto auto auto auto auto;
}

.logos svg{
	margin-left: 3em;

	height: 150px;
}

.titles{
	display: grid;
	grid-template-columns: auto auto auto auto;
	margin-left: auto;
	margin-right: auto;
}

.table, td, th{
	border: none;
	color: #002D76;
	font-size: 20px;
}

.table td{
	height: 200px;
	width: 200px;
}

.table td img{
	height: 200px;
	width: 200px;
}

.header{
	color: #002D76;
	text-align: center;
	font-size: 30px;
}

.button{
	composes: button from './button.module.css';
	color: white;
	background-color: #4B74F4;
	border: none;
	font-size: 20px;
	margin-right: 40vw;
	margin-left: 40vw;
	text-align: center;
	margin-top: 1em;
}

.score{
	font-size: 60px;
	color: #002D76;
	background-image: url("./../assets/SCORE CIRCLE.svg");
	margin-left: auto;
	margin-right: auto;
	margin-top: 1em;
	text-align: center;
	height: 5em;
	background-repeat: no-repeat;
	background-position: center;
	display: grid;
	grid-template-rows: auto auto;

}

.scoreText{
	font-size: 40px;
	align-self: center;

}

.back{
	composes: button;
	margin-top: 4em;
}

.retry{
	composes: button;
	color: white;
	margin-top: 1em;
	background-color: #002D76;
}
